import React from 'react';

const MagicButton = ({
  title,
  icon,
  position,
  handleClick,
  href,
  otherClasses,
}: {
  title: string;
  icon: React.ReactNode;
  position: string;
  handleClick?: () => void;
  href?: string;
  otherClasses?: string;
}) => {
  const commonClasses = `inline-flex h-full w-full cursor-pointer items-center justify-center rounded-lg bg-slate-950 px-7 py-1 text-sm font-medium text-white backdrop-blur-3xl gap-2 ${otherClasses}`;

  // Render a link if href is provided, otherwise render a button
  const Component = href ? 'a' : 'button';

  const componentProps = href
    ? { href, className: `relative inline-flex h-12 w-full overflow-hidden rounded-lg p-[1px] focus:outline-none md:w-60 md:mt-10` }
    : { className: `relative inline-flex h-12 w-full overflow-hidden rounded-lg p-[1px] focus:outline-none md:w-60 md:mt-10`, onClick: handleClick };

  return (
    <Component {...componentProps}>
      <span className="absolute inset-[-1000%] animate-[spin_2s_linear_infinite] bg-[conic-gradient(from_90deg_at_50%_50%,#E2CBFF_0%,#393BB2_50%,#E2CBFF_100%)]" />
      <span className={commonClasses}>
        {position === 'left' && icon}
        {title}
        {position === 'right' && icon}
      </span>
    </Component>
  );
};

export default MagicButton;